.navbar{
    position: absolute;
    width: 100%;
    height: 80px;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    font-size: 1.2rem;
    box-shadow: 0 5px 15px rgba(0,0,0,0.1);
}
.navbar.active{
    background: white;
}
.nav-logo a{
    text-decoration: none;
}
.nav-logo h2{
    /* color: white; */
    color:saddlebrown;
    font-size: 1.4rem;
    position: absolute;
    top: 0;
    left: 0;
    -webkit-transform: translate(90%,112%);
            transform: translate(90%,112%);
}
.nav-logo.active h2{
    color: black;
}
.nav-logo.active .logo{
    color: black;
}
.nav-logo, .nav-links, .nav-social{
    margin: 20px auto;
}
.nav-toggle{
    cursor: pointer;
    position: absolute;
    right: 5%;
    display: none;
}
.nav-toggle > div{
    background-color: saddlebrown;
}
.nav-toggle.active > div{
    background-color: black;
}
.line , .line1 , .line2 , .line3{
    width: 33px;
    /* background-color: white; */
    height: 4px;
    margin: 5px 3px;
    transition: all .3s;
}
.nav-toggle:hover .line, .line1 , .line2, .line3{
    background-color: #49a6e9;
}
.line1{
    -webkit-transform:rotate(45deg) translate(6px,1px);
            transform:rotate(45deg) translate(6px,1px);
}
.line2{
    transition: all .01s;
    visibility: hidden;
}
.line3{
    -webkit-transform:rotate(-45deg) translate(11px,-7px);
            transform:rotate(-45deg) translate(11px,-7px);;
}
.links-list, .links-list-mobile{
    display: grid;
    grid-template-columns: repeat(4,auto);
    grid-gap: 10px;
    list-style: none;
    text-align: center;
    width: 70vw;
    -webkit-justify-content:end;
            justify-content:end;
    margin-right: 1rem;
}
.social-list ,.social-list-mobile{
    list-style: none;
    display: -webkit-flex;
    display: flex;
}
.links-list li, .social-list li, .links-list-mobile li, .social-list-mobile li {
    margin: 0 5px; 
}
.nav-links a{
    color: saddlebrown;
}
.nav-links.active a{
    color: black;
}
.links-list li a, .social-list li a ,.links-list-mobile li a , .social-list-mobile li a{
    text-decoration: none;
    font-size: 1.2rem;
    padding: 8px;
    transition: all .3s;
}
.links-list li a:hover{
    background-color:#49a6e9;
    border-radius: 5px;
    color: white;
}
@media screen and (max-width: 850px){
    /* .navbar{
        position: relative;
    } */
    /* .logo{
        transform: translate(16vw,4.8vw);
    } */
    .links-list{
        display: -webkit-flex;
        display: flex;
        -webkit-flex-direction: column;
                flex-direction: column;
        width: 100%;
        height: 400px;
        background-color: #49a6e9;
        position: absolute;
        top: 80px;
        left: -100%;
