    }
    .links-list-mobile{
        display: -webkit-flex;
        display: flex;
        -webkit-flex-direction: column;
                flex-direction: column;
        width: 100%;
        height: 400px;
        background-color: #49a6e9;
        position: absolute;
        top: 80px;
        left: 0;
        opacity: 1;
        z-index: 1;
        transition: all 0.5s;
    }
    .social-list{
        background-color: white;
        padding: 15px;
        list-style: none;
        display: -webkit-flex;
        display: flex;
        z-index: 1;
        position: absolute;
        width: 100%;
        left: -100%;
        -webkit-justify-content: center;
                justify-content: center;
        top: 425%;
        transition: all 0.5s;
    }
    .social-list-mobile{
        background-color: white;
        padding: 15px;
        list-style: none;
        display: -webkit-flex;
        display: flex;
        margin: 10px auto;
        z-index: 1;
        position: absolute;
        width: 100%;
        left: 0%;
        -webkit-justify-content: center;
                justify-content: center;
        top: 425%;
        transition: all .5s;
    }
    .links-list li, .links-list-mobile li{
        margin-left:-35px; 
    }
    .links-list li a, .links-list-mobile li a{
        text-align: center;
        padding: 1rem;
        width: 100%;
        display: table;
    }
    .links-list-mobile li a:hover , .links-list-mobile li a:hover{
        background-color: #4e9bd3;
        border-radius: 0;
    }
    .nav-toggle{
        display: block;
    }
}
*{
    font-family: 'Josefin Sans', sans-serif;
}
video{
    object-fit: cover;
    height: 100%;
    width: 100%;
    position: fixed;
    z-index: -1;
}
.display{
    height: 100vh;
    width: 100%;
    object-fit: contain;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    box-shadow: inset 0 0 0 1000px rgba(0,0,0,0.2);
}
.display > h1{
    color: white;
    font-size: 80px;
    margin-top: 0px;
}
.display > p{
    color: white;
    font-size: 28px;
    margin-top: 8px;
}
.display-btns{
    margin-top: 28px;
}
.display-btns button{
    margin: 6px;
    cursor: pointer;
}
.btn--outline{
    /* font-size: 25px; */
    font-size: 20px;
    color: white;
    background: none;
    border: 1px solid white;
    font-weight: bolder;
    padding: 10px 20px;
    transition: all .3s ease-in-out;
}
.btn--outline:hover{
    color: black;
    background-color: white;
    border: none;
}
.btn--primary{
    /* font-size: 25px; */
    font-size: 20px;
    color: black;
    background-color: white;
    padding: 10px 20px;
    border:none;
    font-weight: bold;
    transition: all .7s ease-in-out;
}
.btn--primary:hover{
    color: white;
    background: none;
    border: 1px solid white;
}
@media screen and (max-width: 850px){
    .display > h1{
        font-size: 60px;
        margin-top: -130px;
    }
    .display > p{
        font-size: 24px;
        margin-top: 6px;
    }
}
@media screen and (max-width: 640px){
    .display > h1{
        font-size: 40px;
        margin-top: -100px;
    }
    .display > p{
        font-size: 20px;
        margin-top: 8px;
    }
    .display-btns{
        margin-top: 16px;
    }
    .btn--primary, .btn--outline{
        display: block;
        width: 100%;
        font-size: 20px;
    }
}
@media screen and (max-width: 435px){
    .display > h1{
        font-size: 30px;
        margin-top: -150px;
    }
    .display > p{
        font-size: 16px;
    }
    .btn--primary , .btn--outline{
        font-size: 16px;
    }
}
*{
  font-family: 'Josefin Sans', sans-serif;
}
.cards {
  padding: 4rem;
  background: #fff;
}

h1 {
  margin-top: 8%;
  text-align: center;
}

.cards__container {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: column;
          flex-flow: column;
  -webkit-align-items: center;
          align-items: center;
  max-width: 1120px;
  width: 90%;
  margin: 0 auto;
}

.cards__wrapper {
  position: relative;
  margin: 50px 0 45px;
}

.cards__items {
  margin-bottom: 24px;
}

.cards__item {
  display: -webkit-flex;
  display: flex;
  -webkit-flex: 1 1;
          flex: 1 1;
  margin: 0 1rem;
  border-radius: 10px;
}

.cards__item__link {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: column;
          flex-flow: column;
  width: 100%;
  box-shadow: 0 6px 20px rgba(56, 125, 255, 0.17);
  -webkit-filter: drop-shadow(0 6px 20px rgba(56, 125, 255, 0.017));
  filter: drop-shadow(0 6px 20px rgba(56, 125, 255, 0.017));
  border-radius: 10px;
  overflow: hidden;
  text-decoration: none;
}

.cards__item__pic-wrap {
  position: relative;
  width: 100%;
  padding-top: 67%;
  overflow: hidden;
}

.fade-img {
  -webkit-animation-name: fade-img;
          animation-name: fade-img;
  -webkit-animation-duration: 2s;
          animation-duration: 2s;
}

.cards__item__pic-wrap::after {
  content: attr(data-category);
  position: absolute;
  bottom: 0;
  margin-left: 10px;
  padding: 6px 8px;
  max-width: calc((100%) - 60px);
  font-size: 12px;
  font-weight: 700;
  color: #fff;
  background-color: #1f98f4;
  box-sizing: border-box;
}

.cards__item__img {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  display: block;
  width: 100%;
  max-width: 100%;
  height: 100%;
  max-height: 100%;
  object-fit: cover;
  transition: all 0.2s linear;
}

.cards__item__img:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

.cards__item__info {
  width:340px;
  padding: 20px 30px 30px;
}

.cards__item__text {
  color: #252e48;
  font-size: 18px;
  line-height: 24px;
}

@media only screen and (min-width: 1200px) {
  .content__blog__container {
    width: 84%;
  }
}

@media only screen and (min-width: 1024px) {
  .cards__items {
    display: -webkit-flex;
    display: flex;
  }
  .cards__item__info{
    width: 280px;
  }
}

@media only screen and (max-width: 1024px) {
  .cards__item {
    margin-bottom: 2rem;
  }
}
@media only screen and (max-width: 550px){
  .cards__item__info{
    width: 250px;
  }
}

